/* designing the full intro */
.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}
/* left sside design */
.i-left{
    display: flex;
    position: relative;
    flex:1;
    flex-direction: column;
    gap: 2rem;
}
.i-name{
    display: flex;
    flex-direction: column;
}
/* targeting the span tags with special tags 
If we have more than same span tags, so to target we use such tags*/
/* 1st child */
.i-name>:nth-child(1)
{
    color: var(--black);
    font-weight: bold;
    font-size: 4rem;
}
/* 2nd child */
.i-name>:nth-child(2)
{
    color: var(--orange);
    font-size: 3rem;
    font-weight: bold;
}
/* 3rd child or span */
i.name>:nth-child(3)
{
    font-weight: 100;
    font-size: 14px;
    color: var(--gray);
    margin-top: 10px;
}
.i-button{
    width: 6rem;
    height: 2rem;
}
/* styling the images of insta , linedin , github */
.i-icons{
    margin-top: 3rem;
    display: flex;
    gap: 0rem;
}
/* Targettting all the images >* */
.i-icons>*{
    transform: scale(0.5);
}
.i-icons>*:hover{
    cursor: pointer;
}



/* right side design */
.i-right{
    flex: 1;
    position: relative;
}
.i-right>*{
    position: absolute;
    z-index: 1;
}
.i-right>:nth-child(1){
    transform: scale(0.69);
    left: -15%;
    top: -9%;
}
.i-right>:nth-child(2){
    top: -4.6rem;
    transform: scale(0.67);
    left: -3rem;
}
.i-right>:nth-child(3){
    transform: scale(1.0);
    left: 7%;
    top: -1%;
}

.i-right>:nth-child(4){
    transform: scale(0.6);
    left: -21%;
    top: -13%;
    border-radius: 50%;
}

.blur{
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}

@media screen and (max-width: 480px) 
{
   .intro
   {
    flex-direction: column;
    height: 64rem;
    gap: 7rem;
   }
    .i-right
    {
        transform: scale(0.8);
        left: -3rem;
    }
    .i-right .blur
    {
        display: none;
    }
  

  .floating-div:nth-of-type(1)
  {
    top: -7rem !important;
  }

}