.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}

.w-right{
    position: relative;
}

.w-mainCircle{
    left: 9rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow:var(--smboxShadow);
    top: 2rem;
    background: white;
}

.w-mainCircle>*{

    position: absolute;
}

/* setting the position of the images in cirlce */

.w-mainCircle > :nth-child(1){
    top: -3rem;
    left: 6rem;
}
.w-mainCircle > :nth-child(2){
    top: 6rem;
    left: -3rem;
}

.w-mainCircle > :nth-child(3){
    top: 6rem;
    left: 5rem;
}
.w-mainCircle > :nth-child(4){
    top: 15rem;
    left: 5rem;
}
.w-mainCircle > :nth-child(5){
    top: 6rem;
    left: 13rem;
}

/* secondary Circles Design */
.w-secCircle{
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: 'none';
    box-shadow: var(--smboxShadow);
    background: white;

}

/* setting up the size of the png */
.w-secCircle> img{
    transform: scale(0.6);
}

.w-backCircle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}

.blueCircle{
    background: #1949b8;
    left: 18rem;
    top: 0rem;
}


.yellowCircle
{
    background: #f5c32c;
    left: 18rem;
    top: 8rem;
}
@media screen and (max-width: 480px) {
    .works{
      padding: 0.5rem 1rem;
    }
    
  }